import React from 'react'
import Particles from 'react-particles-js';
import {saveAs} from 'file-saver'

const Footer = () => {
  return (
    <div className='footer'>
      <Particles 
              params={{
                "particles": {
                    "number": {
                        "value": 19,
                        "density": {
                            "enable": true,
                            "value_area": 500
                        }
                    },
                    "line_linked": {
                        "enable": false
                    },
                    "move": {
                        "speed": 7,
                        "out_mode": "out"
                    },
                    "shape": {
                        "type": [
                            "images",
                        ],
                        "images": [
                            {
                                "src": "static/img/icons/github.svg",
                                "height": 20,
                                "width": 23
                            },
                            {
                                "src": "static/img/icons/blue-php.svg",
                                "height": 20,
                                "width": 23
                            },
                            {
                                "src": "static/img/icons/blue-css.svg",
                                "height": 20,
                                "width": 23
                            },
                            {
                                "src": "static/img/icons/blue-js.svg",
                                "height": 20,
                                "width": 23
                            },
                            {
                                "src": "static/img/icons/blue-html.svg",
                                "height": 20,
                                "width": 23
                            },
                            {
                                "src": "static/img/icons/blue-react.svg",
                                "height": 20,
                                "width": 23
                            },
                        ]
                    },
                    "color": {
                        "value": "#CCC"
                    },
                    "size": {
                        "value": 30,
                        "random": false,
                        "anim": {
                            "enable": true,
                            "speed": 4,
                            "size_min": 12,
                            "sync": false
                        }
                    }
                },
                "retina_detect": false
            	}}
              style={{
                width: '100%',
                wheight: '100%',
                position:'absolute',
                top:0,
                left:0
              }}
            />
        <div className="content-wrapper">
           <div className="sitemap">
             <div className="item logo-section responsive">
                <img className='logo' src='static/img/logo.png'/>
                <div className="resume" onClick={() => saveAs('static/resume.pdf', 'CV_Gherciu_Gheorghe.pdf')}>
                    <img className="resume-icon" src="static/img/icons/resume.svg" />
                    <span>Resume</span>
                </div>
             </div>
             <div className="item">
                <div className="site-anchors">
                    <div>
                        <b><a href="#about">About</a></b>
                        <b><a href="#works">Works</a></b>
                        <b><a target='_blank' href="https://www.npmjs.com/~gherciu_gheorghe">NPM</a></b>
                    </div>
                    <div>
                        <b><a target='_blank' href="https://github.com/Gherciu">GitHub</a></b>
                        <b><a target='_blank' href="https://codepen.io/gherciu">CodePen</a></b>
                        <b><a target='_blank' href="https://www.linkedin.com/in/gheorghe-gherciu-346949165/">LinkedIn</a></b>
                    </div>
                </div>
             </div>
           </div>
           <div className="copyright">
                Â© 2019 Gherciu Gheorghe. All Rights Reserved
           </div>
        </div>
        <style jsx>{`
            .footer{
                background: linear-gradient(45deg,#6d4dd9,#14badc);;
                min-height:130px;
                width:95%;
                margin: 0 auto;
                border-radius:200px 200px 0px 0px;
                color:#fff;
                border-top:12px solid #E0E5F6;
                position:relative;
                overflow: hidden;
            }
            .content-wrapper{
                padding:60px 10px 20px!important;
            }
            .sitemap{
                display: flex;
                justify-content: space-between;
            }
            .sitemap .item{
                flex:1;
                min-height:60px;
                margin-bottom: 10px;
                display: flex;
                flex-direction:column;
                align-items: flex-start;
            }
            .sitemap .item:last-of-type{
                align-items: flex-end;
            }
            .sitemap .item i a{
                text-decoration:none;
                color:#fff;
            }

            .sitemap .item  b a{
                text-decoration:none;
                color:#fff;
            }
            .site-anchors{
                display: flex;
                flex-direction:row;
                align-items: flex-start;
            }

            .site-anchors div{
                display: flex;
                flex-direction:column;
                align-items: flex-start;
            }

            .site-anchors div:last-of-type {
                display: flex;
                flex-direction:column;
                align-items: flex-start;
                padding-left: 30px;
            }

            .resume {
                display: flex;
                align-items: center;
                cursor: pointer;
                background: #fff;
                border-radius: 24px;
                margin: 15px 0px;
                filter: drop-shadow(0 20px 10px rgba(0, 0, 0, 0.3));
            }
            .resume-icon {
                width: 25px;
                margin: 10px 12px 10px 18px;
            }
            .resume span {
                font-weight: 500;
                font-style: italic;
                color: #5A81ED;
                margin-right: 18px;
                margin-bottom: 2px;
            }

            @media (max-width:1250px){
                .sitemap .item{
                    align-items: center;
                }
                .sitemap .item:last-of-type{
                    align-items: center;
                }
                .copyright{
                    margin-left:80px;
                }
                .item.logo-section  {
                    align-items: flex-start;
                }
                
            }
            @media (max-width:840px){
                    .footer{
                        border-radius: 0px;
                        margin:0px;
                        width: 100vw;
                    }
            }
            @media (max-width:500px){
                .sitemap{
                    display: flex;
                    flex-direction: column;
                }
              
                .copyright{
                    margin: 10px;
                    text-align: center;
                }
            }
        `}</style>
    </div>
  )
}

export default Footer
